body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.card {
  width: 100px;
  height: 150px;
  color: black;
  position: relative;
  margin: 10px;
}

.card--winner {
  margin-top: -10px;
  -webkit-animation: winner-jump 3s;
          animation: winner-jump 3s;
}

@-webkit-keyframes winner-jump {
  from {margin-top: 0px;}
  to {margin-top: -10px;}
}

@keyframes winner-jump {
  from {margin-top: 0px;}
  to {margin-top: -10px;}
}

.card__front {
  display: none;
}

.card__back {
  display: block;
}

.card__img {
  position: absolute;
  left: 5px;
  top: 8px;
  width: 100px;
}

.card__img--back {
  position: absolute;
  left: 0;
  top: 0;
  width: 100px;
  border: 5px solid;
  border-color: black;
  border-radius: 20px;
}

.hand {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.hand__cards {
  display: flex;
  flex-direction: row;
  justify-content: space-between;

  background: beige;
  padding: 20px 35px 30px 20px;
  margin: 20px;
}

.hand__button {
  margin: auto;
}

.hand__button--hand {
  height: 100px;
  width: 100px;
  border-radius: 10px;
  background: beige;
}

.hand__button--community {
  background: green;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

